.AudioPlayer_progressBar__1gNLL {
  /* default, for chrome */
  box-shadow: var(--box-shadow);
  --bar-bg: rgb(225, 225, 225);
  --seek-before-width: 0%;
  --seek-before-color: black;
  --playhead: rgba(0, 0, 0, 0.75);
  --selected-playhead: rgba(0, 0, 0, 0.5);

  -webkit-appearance: none;

          appearance: none;
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  height: 0.7rem;
  outline: none;
  width: 100%;
}

.AudioPlayer_progressBar__1gNLL:hover,
.AudioPlayer_playPause__1YTuI:hover {
  cursor: pointer;
  box-shadow: transparent;
}

/* progress bar - safari */

.AudioPlayer_progressBar__1gNLL::-webkit-slider-runnable-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 0.7rem;
  outline: none;
}

/* progress bar - firefox */

.AudioPlayer_progressBar__1gNLL::-moz-range-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 11px;
  outline: none;
}

.AudioPlayer_progressBar__1gNLL::-moz-focus-outer {
  border: 0;
}

/* progress bar - chrome and safari */
.AudioPlayer_progressBar__1gNLL::before {
  content: "";
  height: 11px;
  width: var(--seek-before-width);
  background-color: var(--seek-before-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  /* position: absolute; */
  top: 0;
  left: 0;
  z-index: 2;
}

/* progress bar - firefox */
.AudioPlayer_progressBar__1gNLL::-moz-range-progress {
  background-color: var(--seek-before-color);
  border-top-left-radius: 10pacrx;
  border-bottom-left-radius: 10px;
  height: 11px;
}

/* playhead - chrome and safari*/
.AudioPlayer_progressBar__1gNLL::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: none;
  background-color: var(--playhead);
  position: relative;
  margin: -2px 0 0 0;
  z-index: 3;
  box-sizing: border-box;
}

/*playhead while dragging - chrome and safari */
.AudioPlayer_progressBar__1gNLL:active::-webkit-slider-thumb {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  background: var(--selected-playhead);
}

/*playhead - firefox*/
.AudioPlayer_progressBar__1gNLL::-moz-range-thumb {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: transparent;
  background-color: var(--playhead);
  position: relative;
  margin: -2 0 0 0;
  z-index: 3;
  box-sizing: border-box;
}
/*playhead while dragging - firefox */
.AudioPlayer_progressBar__1gNLL:active::-moz-range-thumb {
  transform: scale(1.2);
  background: var(--selected-playhead);
}

